import { Inline } from '@ali/4ever-cangjie';
import { JsonML, getTagName, getAttributes } from '@ali/4ever-utils';
import Refer from '../models';

export default {
  name: 'refer',

  match(node: JsonML): boolean {
    return (
      getTagName(node) === 'span' &&
      getAttributes(node)['data-type'] === 'refer'
    );
  },

  convert(node: JsonML): Inline {
    const data = { ...getAttributes(node) };
    delete data['data-type'];
    return Refer.create({
      data,
    });
  },
};
